.jdo-title {
    font-size: 1.5em;
    font-weight: bold;
    margin-bottom: 0.5em;
    text-align: center;
}
.jdo-title > h1 {
    color: var(--violet);
    font-family: 'Ubuntu Mono', sans-serif;
}

.jdo-content{
    width: 50%;
    height: 100vh;
    overflow: hidden;

    display: flex;
    gap: 25vmin;
}

.jdo-content > .p{
    position: absolute;
    top: 48%;
    left: 70%;
    width: 50%;
    transform: translate(-50%, -50%);
    color: var(--bleu-clair);
    font-family: 'Ubuntu Mono', sans-serif;
    font-size: 1.5em;
    
}

.jdo-content > .jdo-img {
    position: absolute;
    top: 50%;
    left: 20%;
    width: 20%;
    transform: translate(-50%, -50%);
}

.jdo-content > .p > .condition {
    position: absolute;
     transform: translateY(200%);
    color: var(--rose-pale);
    font-family: 'Ubuntu Mono', sans-serif;
    font-size: .5em;
}

#source-link {
    bottom: 60px;
}
  
  
  
  .meta-link {
    align-items: center;
    backdrop-filter: blur(3px);
    background-color: rgba(240, 188, 208, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 6px;
    bottom: 10px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
    cursor: pointer;  
    display: inline-flex;
    gap: 5px;
    right: 60px;
    padding: 10px 20px;
    position: fixed;
    text-decoration: none;
    transition: background-color 400ms, border-color 400ms;
    z-index: 10000;
  }
  
  .meta-link:hover {
    background-color: rgba(240, 188, 208, 0.8);
    border: 1px solid rgba(255, 255, 255, 0.2);
  }
  
  .meta-link > i, .meta-link > span {
    height: 20px;
    line-height: 20px;
  }
  
  #source-link > span {
    color: #000;
    font-weight: 400;
    top: 50%;
    transform: translateY(-10%);
  }

  .meta-link > .jdo-icon {
    color: var(--violet);
  }