@import url('https://fonts.googleapis.com/css2?family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&family=Ubuntu+Mono&display=swap');

:root{
  --violet: #cdb4db;
  --rose-pale: #ffc8dd;
  --rose: #ffafcc;
  --bleu-clair: #bde0fe;
  --bleu: #a2d2ff;
}

*{
    cursor: default;
}

.about-container {
  text-align: center;
}

.about {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Hero{
    position: absolute;
    right: 8em;
    width: 700px;
}
.Cedric{
    position: absolute;
    right: 8em;
    width: 700px;
}
.Thomas{
    position: absolute;
    right: 8em;
    width: 700px;
}
.mainP{
    font-size: 1em;
    font-family: 'Ubuntu Mono', sans-serif;
    color: var(--rose-pale);
    text-align: left;
}

.about-buttons{
  position: absolute;
  top: 0;
}
.about-buttons > a{
  padding: 20px;
  color: var(--rose);
  text-decoration: none;
}
#enabled:hover{
  color: #FFF;
}

#disabled{
    opacity: 0.5;
}

.slideDown{
    position: relative;
    transform: translateX(320px);
    color: var(--bleu);
    font-size: 2em;
    cursor: pointer;
}
.slideUp{
    position: relative;
    transform: translateX(320px);
    color: var(--bleu);
    font-size: 2em;
    cursor: pointer;
}

.slideDown:hover{
    color: #FFF;
}

#no{
    opacity: 0;
}

button{
    background-color: #000000;
    border: none;
    cursor: default;
}

.progressbar{
    position: absolute;
    top: 50%;
    left: 10em;
    width: 5px;
    height: 850px;
    background-color: var(--rose);
    z-index: 1;
    transform: translateY(-50%);
}

.progressbar > .bubble{
    position: absolute;
    top: 50%;
    left: -15px;
    width: 35px;
    height: 35px;
    background-color: var(--bleu);
    border-radius: 50%;
    z-index: 2;
}
.progressbar > .bubble1{
    position: absolute;
    top: 80%;
    left: -15px;
    width: 35px;
    height: 35px;
    background-color: var(--bleu);
    border-radius: 50%;
    z-index: 2;
}
.progressbar > .bubble2{
    position: absolute;
    top: 85%;
    left: -15px;
    width: 35px;
    height: 35px;
    background-color: var(--bleu);
    border-radius: 50%;
    z-index: 2;
}
.progressbar > .bubble3{
    position: absolute;
    top: 90%;
    left: -15px;
    width: 35px;
    height: 35px;
    background-color: var(--bleu);
    border-radius: 50%;
    z-index: 2;
}
.progressbar > .bubble4{
    position: absolute;
    top: 95%;
    left: -15px;
    width: 35px;
    height: 35px;
    background-color: var(--bleu);
    border-radius: 50%;
    z-index: 2;
}

#active{
    top: 50%;
    transform: translateY(-50%) scale(1);
    /* smooth translate */
    transition-duration: 1s;
}
#up1{
    position: relative;
    top: 0;
    transform: translateY(50%) scale(.5);
    /* smooth translate */
    transition-duration: 1s;
}

#down{
    transform: translateY(0%) scale(.5);
    /* smooth translate */
    transition-duration: 1s;
}

.img{
    width: 340px;
    height: 340px;

    position: absolute;
    top: 47%;
    left: 0;

    transform: translate(-115%, -50%);
}