@import url('https://fonts.googleapis.com/css2?family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&family=Ubuntu+Mono&display=swap');

:root{
  --violet: #cdb4db;
  --rose-pale: #ffc8dd;
  --rose: #ffafcc;
  --bleu-clair: #bde0fe;
  --bleu: #a2d2ff;
}


.container {
  text-align: center;
}

.landingPage {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#title{
  font-size: 3em;
  font-family: 'Ubuntu Mono', monospace;
  color: var(--rose-pale);
  letter-spacing: 5px;

}

#splash{
  font-size: 1em;
  font-family: 'Space Mono', monospace;
  color: var(--bleu-clair);

  /* animation */
  -webkit-animation: breathing 5s ease-out infinite normal;
  animation: breathing 5s ease-out infinite normal;

  
}

.buttons{
  position: absolute;
  top: 0;
}
.buttons > a{
  padding: 20px;
  color: var(--rose);
  text-decoration: none;
}
a:hover{
  color: #FFF;
}


@-webkit-keyframes breathing {
  0%{
    -webkit-transform: scale(0.9) rotate(-15deg) translate(300px, -105px);
    transform: rotate(-15deg) translate(300px, -105px) scale(0.9);

  } 
  25% {
    -webkit-transform: rotate(-15deg) translate(300px, -105px) scale(1);
    transform: rotate(-15deg) translate(300px, -105px) scale(1);
  }
  60% {
    -webkit-transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
    transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
  } 
  100% {
    -webkit-transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
    transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
  }
}

@keyframes breathing {
  0% {
    -webkit-transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
    -ms-transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
    transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
  }
  25% {
    -webkit-transform: rotate(-15deg) translate(300px, -105px) scale(1);
    -ms-transform: rotate(-15deg) translate(300px, -105px) scale(1);
    transform: rotate(-15deg) translate(300px, -105px) scale(1);
  }
  60% {
    -webkit-transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
    -ms-transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
    transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
  }
  100% {
    -webkit-transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
    -ms-transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
    transform: rotate(-15deg) translate(300px, -105px) scale(0.9);
  }
}